import React, {useEffect, useRef} from 'react';

import {Button, Grid, IconButton, Typography} from "@material-ui/core";
import {CloseRounded} from "@material-ui/icons";

import {Dialog, DialogContent, DialogTitle, DialogActions} from '@material-ui/core';
import SettingSelector from "./SettingSelector";

const SettingSelectDialog = ({ fn_ref, title, onAction }) => {
    const [open, setOpen] = React.useState(false);
    const ref = useRef();
    const handleClose = async () => {
        setOpen(false);
    }

    const handleOpen = async () => {
        if(!!ref.current.name) {
            let data = await ref.current.settingClient.getSetting(ref.current.name);
            onAction(ref.current.settingClient, data);
        }
        setOpen(false);
    }

    useEffect(() => {
        fn_ref.current = {
            open: () => setOpen(true)
        }
    }, [])
    return (
        <Dialog open={open}
                aria-labelledby="customized-dialog-title">
            <DialogTitle id="customized-dialog-title">
                <Grid container>
                    <Grid item xs={11}>
                        <Typography bold variant="h4">
                            {title}
                        </Typography>
                    </Grid>
                    <Grid item xs={1}>
                        <IconButton sx={{position: 'absolute'}} onClick={handleClose}>
                            <CloseRounded />
                        </IconButton>
                    </Grid>
                </Grid>
            </DialogTitle>
            <DialogContent dividers>
                <Grid container>
                    <SettingSelector selector_ref={ref}/>
                </Grid>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleOpen}>Open</Button>
            </DialogActions>
        </Dialog>
    )
}

export default SettingSelectDialog;