import "./SchedulePage.css";
import {useEffect, useRef, useState} from "react";
import {
    Grid,
    IconButton,
    InputBase, List, ListItem, ListItemText,
    Paper, Typography
} from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";
import HiddenMenu from "./components/HiddenMenu";
import SettingClient from "./components/SettingClient";
import AddIcon from '@material-ui/icons/Add';
import TaskCard from "./components/TaskCard";
import SummaryCard from "./components/SummaryCard";
import ContextInformation from "./components/ContextInformation";

function SchedulePage() {
    const ref = useRef();
    const [filter, setFilter] = useState("");
    const [settingList, setSettingList] = useState([]);
    const [grabbingItem, setGrabbingItem] = useState(null);
    const [mousePosition, setMousePosition] = useState([0, 0]);
    const [settingLists, setSettingLists] = useState([{parallel: 25, epoch: 2, settings: []}]);
    const [timerUpdate, setTimerUpdate] = useState(false);
    const [monitorUpdate, setMonitorUpdate] = useState(false);
    const [monitor, setMonitor] = useState([]);

    useEffect(() => {
        ref.current.client = new SettingClient("http://localhost:51223");
        ref.current.setHovering = {};
    });

    useEffect(async () => {
        setSettingList(await ref.current.client.getSettingList(filter));
    }, [filter]);

    useEffect(async () => {
        let schedule = await ref.current.client.getSchedule();
        setSettingLists(schedule.data);
    }, []);

    useEffect(async() => {
        let monitor = await ref.current.client.getMonitor();
        setMonitor(monitor.data);
    })

    useEffect(() => {
        setTimeout(async () => {
            let schedule = await ref.current.client.getSchedule();
            setSettingLists(schedule.data);
            setTimerUpdate((f) => !f);
        }, 5000)
    }, [timerUpdate]);

    useEffect(() => {
        setTimeout(async () => {
            let monitor = await ref.current.client.getMonitor();
            setMonitor(monitor.data);
            setMonitorUpdate(f => !f);
        }, 60000)
    }, [monitorUpdate]);

    useEffect(async () => {
        console.log(settingLists);
        await ref.current.client.setSchedule(settingLists);
    }, [settingLists])

    const grabHandle = (e, index) => {
        setGrabbingItem(index);
        setMousePosition([e.clientX, e.clientY]);
    }
    const releaseHandle = (e) => {
        let i = 0;
        const elem = document.getElementById(`drop-point-${i}`);
        const rect = elem.getBoundingClientRect();
        if(rect.left < e.clientX && e.clientX < rect.right && rect.top < e.clientY && e.clientY < rect.bottom) {
            document.getElementById(`drop-point-inner-${i}`).style.background = "none";
            setSettingLists((ls) => {
                if(!ls[i].settings.includes(settingList[grabbingItem]))
                    ls[i].settings.push(settingList[grabbingItem]);
                return JSON.parse(JSON.stringify(ls));
            });
        }

        setGrabbingItem(null);
    }
    const dragHandle = (e) => {
        setMousePosition([e.clientX, e.clientY]);
        const elem = document.getElementById("drop-point-0");
        const rect = elem.getBoundingClientRect();

        document.getElementById("drop-point-inner-0").style.background = rect.left < e.clientX && e.clientX < rect.right && rect.top < e.clientY && e.clientY < rect.bottom ? "lightskyblue" : "none";
    }

    return (
        <div>
            <div style={{background: "#282c34", position: "fixed", top: "7%", left: 0, width: "25%", height: "95%", tableLayout: "fixed"}}>
                <Paper style={{ margin: "5%"}}>
                    <InputBase
                        sx={{ ml: 1, flex: 1 }}
                        style={{margin: "2% 5%", width: "100%"}}
                        placeholder="Filter"
                        inputProps={{ 'aria-label': 'filter' }}
                        onChange={(e) => setFilter(e.target.value) }
                    />
                </Paper>

                <Paper style={{margin: "5%", height: "72%", tableLayout: "fixed"}}>
                    <List style={{overflow: 'auto', height: "100%", maxHeight: "100%"}}>
                        {
                            settingList.map((v, i) => {
                                return <ListItem className={"item"} onMouseDown={(e) => grabHandle(e, i)}>
                                    <ListItemText style={{cursor: "default"}} className={"noselect"}>
                                        {v}
                                    </ListItemText>
                                </ListItem>
                            })
                        }
                    </List>
                </Paper>
            </div>
            <div id="drop-point" style={{position: "fixed", top: "7%", left: "25%", width: "75%", height: "95%", tableLayout: "fixed"}}>
                <Grid container spacing={2} style={{height: "100%", padding: "2%"}}>
                    <Grid item xs={6} md={12}>
                        <div style={{display: "flex", padding: "5% 5%"}}>
                            {
                                monitor.map(v => <SummaryCard title={v.title} value={v.value} unit={v.unit} />)
                            }
                        </div>
                    </Grid>
                    <Grid item xs={6} md={12}>
                        <Typography>
                            Task Queue
                        </Typography>
                        <div style={{overflow: "auto", padding: "5%", alignItems: "center", display: "flex"}}>
                            <IconButton onClick={() => {
                                setSettingLists((ls) => {
                                    return [{parallel: 25, epoch: 2, settings: []}, ...ls];
                                })
                            }}>
                                <AddIcon />
                            </IconButton>
                            <div style={{display: "flex", height: "100%", tableLayout: "fixed"}}>
                                {
                                    settingLists.map((v, i) => {
                                        return <TaskCard style={{width: "200px"}} index={i} settingLists={settingLists} setSettingLists={setSettingLists} removeItem={(index, j) => {
                                            setSettingLists((ls) => {
                                                ls[index].settings.splice(j, 1);
                                                return JSON.parse(JSON.stringify(ls));
                                            })
                                        }} handleClose={(i) => {
                                            setSettingLists((ls) => {
                                                ls.splice(i, 1);
                                                return JSON.parse(JSON.stringify(ls));
                                            })
                                        }} />
                                    })
                                }
                            </div>

                        </div>
                    </Grid>
                </Grid>
            </div>

            <HiddenMenu ref={ref} />

            <header style={{position: "fixed", top: 0, height: "10%", inset: 0}}>
                <Paper>

                    <div>
                        <IconButton onClick={(e) => ref.current.handleClick(e)} style={{borderRadius: 0}}>
                            <MenuIcon />
                        </IconButton>
                    </div>
                </Paper>
            </header>


            {(grabbingItem !== null) && <div onMouseMove={dragHandle} onMouseUp={(e) => releaseHandle(e)}
                                      style={{top: mousePosition[1] - 500, left: mousePosition[0] - 500, position: "fixed", width: "1000px", height: "1000px",
                                        display: "flex", justifyContent: "center", alignItems: "center"}}>
                <Paper className={"noselect"} style={{padding: "1%", opacity: 0.5}}>{settingList[grabbingItem]}</Paper>
            </div>}
        </div>
    );
}

export default SchedulePage;