import "./SettingPage.css";
import Form from "@rjsf/material-ui";
import {Button, Paper, Tab, Tabs, IconButton, Menu, MenuItem, Fade} from "@material-ui/core";
import MenuIcon from '@material-ui/icons/Menu';
import {useEffect, useRef, useState} from "react";

import axios from "axios";
import SettingSelectDialog from "./components/SettingSelectDialog";
import SettingClient from "./components/SettingClient";
import HiddenMenu from "./components/HiddenMenu";
import {Link} from "react-router-dom";

function SettingPage() {
    const ref = useRef();
    const saveRef = useRef();
    const menuRef = useRef({});
    const [tabIndex, setTabIndex] = useState(1);
    const [taskData, setTaskData] = useState({});
    const [creatorData, setCreatorData] = useState({});
    const [deleterData, setDeleterData] = useState({});
    const [solverData, setSolverData] = useState({});
    const [geneticAlgorithmData, setGeneticAlgorithmData] = useState({});
    const [otherData, setOtherData] = useState({});
    const handleChange = (event, newValue) => {
        setTabIndex(newValue);
    };
    const openDialog = () => {
        ref.current.open();
    }
    const onSubmit = (event) => {
        const json = {
            tasks : taskData,
            creator : creatorData,
            deleter : deleterData,
            main_network : solverData,
            genetic_algorithm : geneticAlgorithmData,
            others : otherData
        };
        saveRef.current.client.addSetting(json);
    }
    const [taskSchema, setTaskSchema] = useState({});
    const [taskUISchema, setTaskUISchema] = useState({});
    const [networkSchema, setNetworkSchema] = useState({});
    const [geneticAlgorithmSchema, setGeneticAlgorithmSchema] = useState({});
    const [otherSchema, setOtherSchema] = useState({});
    const [otherUISchema, setOtherUISchema] = useState({});

    useEffect(() => {
        axios.get("/schema/task.schema.json").then(r => setTaskSchema(r.data));
        axios.get("/schema/task.ui.schema.json").then(r => setTaskUISchema(r.data));
        axios.get("/schema/network.schema.json").then(r => setNetworkSchema(r.data));
        axios.get("/schema/genetic_algorithm.schema.json").then(r => setGeneticAlgorithmSchema(r.data));
        axios.get("/schema/other.schema.json").then(r => setOtherSchema(r.data));
    }, [])
    const forms = [
        [taskSchema, taskUISchema, taskData, setTaskData],
        [networkSchema, taskUISchema, creatorData, setCreatorData],
        [networkSchema, taskUISchema, deleterData, setDeleterData],
        [networkSchema, taskUISchema, solverData, setSolverData],
        [geneticAlgorithmSchema, taskUISchema, geneticAlgorithmData, setGeneticAlgorithmData],
        [otherSchema, otherUISchema, otherData, setOtherData]
    ];

    useEffect(() => {
        saveRef.current = {
            client: new SettingClient("http://localhost:51223")
        };
    }, []);
    return (
        <div>
            <div className="SettingPage">
                {forms.map(
                    ([schema, ui_schema, data, setData], index) =>
                        <div role="tabpanel"
                             hidden={tabIndex !== index + 1}>
                            <Form schema={schema} uiSchema={ui_schema}
                                  formData={data} onChange={ (event) =>  setData(event.formData) }
                                  onSubmit={onSubmit}/>
                        </div>)}
            </div>
            <HiddenMenu ref={menuRef} />

            <header style={{position: "fixed", top: 0, height: "10%", inset: 0}}>
                <Paper>
                    <Tabs value={tabIndex}
                          onChange={handleChange}>
                        <div>
                            <IconButton onClick={(e) => menuRef.current.handleClick(e)} style={{borderRadius: 0}}>
                                <MenuIcon />
                            </IconButton>
                        </div>

                        <Tab label="Task"/>
                        <Tab label="Creator"/>
                        <Tab label="Deleter"/>
                        <Tab label="Solver" />
                        <Tab label="Genetic Algorithm" />
                        <Tab label="Other" />
                        <div style={{width: "100%", display: "flex", justifyContent: "flex-end", alignItems: "center"}} id={"sample"}>
                            <div style={{marginRight: "5%"}}>
                                <Button  onClick={openDialog}>Open</Button>
                            </div>
                        </div>
                    </Tabs>
                </Paper>
            </header>

            <SettingSelectDialog fn_ref={ref} title="Open File" onAction={async (client, data) => {
                forms[0][3](data.tasks);
                forms[1][3](data.creator);
                forms[2][3](data.deleter);
                forms[3][3](data.main_network);
                forms[4][3](data.genetic_algorithm);
                forms[5][3](data.others);
            }}/>
        </div>
    );
}

export default SettingPage;