
import {useState} from "react";
import {IconButton, List, ListItem, ListItemSecondaryAction, ListItemText, Paper, TextField} from "@material-ui/core";
import DeleteIcon from '@material-ui/icons/Delete';
import CloseIcon from '@material-ui/icons/Close';

function TaskCard({index, handleClose, settingLists, setSettingLists, removeItem}) {
    return (
        <Paper style={{width: "200px", padding: "1%", marginLeft: "2%"}}>
            <div style={{display: "flex"}}>
                <TextField value={settingLists[index].parallel} label="parallel" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}
                           onChange={(e) => {
                               setSettingLists(ls => {
                                   ls[index].parallel = parseInt(e.target.value, 10) || 0;
                                   return JSON.parse(JSON.stringify(ls));
                               });
                           }} />
                <TextField value={settingLists[index].epoch} label="epoch" inputProps={{ inputMode: 'numeric', pattern: '[0-9]*' }}
                           onChange={(e) => {
                               setSettingLists(ls => {
                                   ls[index].epoch = parseInt(e.target.value, 10) || 0;
                                   return JSON.parse(JSON.stringify(ls));
                               })
                           }} />
                <IconButton onClick={() => {
                    handleClose();
                }}>
                    <CloseIcon />
                </IconButton>
            </div>
            <List dense={true}>
                {settingLists[index].settings.map((v, i) => {
                    return <ListItem>
                        <ListItemText primary={v} style={{overflow: "hidden"}} />
                        <ListItemSecondaryAction>
                            <IconButton edge="end" aria-label="delete" onClick={() => {
                                removeItem(index, i);
                            }}>
                                <DeleteIcon style={{color: "gray"}} />
                            </IconButton>
                        </ListItemSecondaryAction>
                    </ListItem>
                })}
            </List>
            <div id={`drop-point-${index}`} style={{width: "100%", height: "30px", tableLayout: "fixed", display: "flex", alignItems: "center"}}>
                <div id={`drop-point-inner-${index}`} style={{width: "100%", height: "50%", tableLayout: "fixed"}} />
            </div>

        </Paper>
    )
}

export default TaskCard;