
import {useEffect, useRef, useState, useImperativeHandle, forwardRef} from "react";
import {Menu, MenuItem} from "@material-ui/core";
import {Link} from "react-router-dom";

const HiddenMenu = forwardRef((props, ref) => {
    const [anchorEl, setAnchorEl] = useState(null);
    const open = Boolean(anchorEl);
    useImperativeHandle(ref, () => ({
        handleClick: (event) => {
            setAnchorEl(event.currentTarget);
        }}));


    const handleClose = () => {
        setAnchorEl(null);
    };

    return (
        <Menu
            id="demo-positioned-menu"
            aria-labelledby="demo-positioned-button"
            anchorEl={anchorEl}
            open={open}
            onClose={handleClose}
            anchorOrigin={{
                vertical: 'top',
                horizontal: 'left',
            }}
            transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
            }}>
            <MenuItem component={Link} to="/setting">Setting</MenuItem>
            <MenuItem component={Link} to="/schedule">Schedule</MenuItem>
            <MenuItem component={"a"} href="http://localhost:8889">Jupyter</MenuItem>
        </Menu>
    )
});

export default HiddenMenu;