import React, {useEffect, useState} from 'react';
import {Box, Button, Grid, Input, Paper, Typography} from "@material-ui/core";
import SettingClient from "./SettingClient";
import SettingItem from "./SettingItem";

function FileSelector({ selector_ref }) {
    const [items, setItems] = useState([]);
    const [selected, setSelected] = useState(null);
    const [filter, setFilter] = useState("");
    const onItemDoubleClick = async (name, index) => {

    };
    const onItemClick = async (name, index) => {
        setSelected(index);
        selector_ref.current.name = name;
    };

    useEffect(() => {
        selector_ref.current = {
            name: null,
            settingClient: new SettingClient('http://localhost:51223')
        };
    }, []);

    useEffect(() => {
        (async () => {
            const items = await selector_ref.current.settingClient.getSettingList(filter);
            setItems(items);
        })()
    }, [filter])

    return (
        <div>
            <Paper style={{padding: 10}}>
                <Box sx={{width: "400px", height: "250px"}} overflow="auto">
                    { items.map((item, index) => index === selected ?
                        <SettingItem name={item} onDoubleClick={onItemDoubleClick} onClick={onItemClick} index={index} background={"skyblue"} />
                      : <SettingItem name={item} onDoubleClick={onItemDoubleClick} onClick={onItemClick} index={index} background={"none"} />) }
                </Box>
            </Paper>
            <Grid container>
                <Grid item xs={8} container alignContent="center">
                    <Paper style={{width: "300px", paddingLeft: "2%", paddingRight: "2%"}}>
                        <Input style={{textOverflow: "ellipsis", overflow: "hidden", whiteSpace: "nowrap", width: "100%"}}
                               value={filter}
                               onChange={(event) => { setFilter(event.target.value); }}/>
                    </Paper>
                </Grid>
                <Grid item xs={4} container justify="flex-end">

                </Grid>
            </Grid>
        </div>
    )
}

export default FileSelector;