import axios from "axios";

class SettingClient {
    constructor(url) {
        this.url = url;
        this.session = axios.create({
            baseURL: url,
            responseType: 'json'
        });
    }

    async getSettingList(filter="") {
        let ret = [];
        await this.session.get("/", {
            params: { "filter": filter }
        }).then((response) => {
                ret = response.data["items"];
            });
        return ret;
    }

    async getSetting(name) {
        let content = {};
        await this.session.get("/content", {
            params: { "name": name }
        }).then((response) => {
            content = response.data
        });
        return content;
    }

    async addSetting(data) {
        await this.session.post("/", {
            "name": data["others"]["name"],
            "data": data
        }).then(({response}) => {
            alert("Succeed");
        }).catch(({response}) => {
            alert(`${data["others"]["name"]} has already existed.`);
        });
    }

    async getSchedule() {
        return await this.session.get("/schedule");
    }

    async setSchedule(data) {
        return await this.session.post("/schedule", data);
    }

    async getMonitor() {
        return await this.session.get("/monitor");
    }
}

export default SettingClient;