import './SettingItem.css';
import React, {useState} from 'react';
import {Grid, Input, Typography} from "@material-ui/core";

function SettingItem({ name, onClick, onDoubleClick, index, background }) {
    let clickCount = 0;
    const click = () => {
        clickCount++;
        if (clickCount < 2) {
            setTimeout(() => {
                if (clickCount > 1) {
                    onDoubleClick(name, index);
                } else {
                    onClick(name, index);
                }
                clickCount = 0;
            }, 200);
        }
    };

    return (
        <div>
            <Grid container
                  onClick={click}
                  className="item">
                <Grid item xs={12}>
                    <Typography style={{cursor: "default", background: background }}>
                        {name}
                    </Typography>
                </Grid>
            </Grid>
        </div>
    )
}

SettingItem.defaultProps = {
    onClick: (name, index) => {},
    onDoubleClick: (name, index) => {}
};

export default SettingItem;